ul {
  list-style: none;
}

input {
  border-radius: 5px;
  border: 1.5px solid #777;
  margin-bottom: 40px;
  padding: 15px;
  height: 9px;
  outline: none;
  font-size: 22px;
  font-family: 'Montserrat', sans-serif;
  width: 70%;
}

input::-webkit-input-placeholder {
  font-size: 22px;
  color: #ccc;
}

input::-ms-input-placeholder {
  font-size: 22px;
  color: #ccc;
}

input::placeholder {
  font-size: 22px;
  color: #ccc;
}

button {
  font-family: 'Montserrat', sans-serif;
  padding: 8.5px 25px;
  border-radius: 5px;
  font-size: 1.2rem;
  font-weight: 700;
  margin-left: 15px;
  background-color: #7AD2D1;
  border: none;
  color: #fff;
  box-shadow: 0px 4px 10px 0px rgba(100, 100, 100, 0.7);
}

.header {
  grid-column: span 2;
  background-color: #7AD2D1;
  font-family: 'Lobster', cursive;
  font-size: 2.3rem;
  color: #fff;
} 

.header header {
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 10px;
  margin-left: 5px;
}

.header header img {
  margin-right: 10px;
}

.app {
  font-family: 'Montserrat', sans-serif;
  display: grid;
  grid-template-rows: minmax(5%, 10%);
  grid-template-columns: minmax(50%, 50%);
  border: 2px solid #333;
  height: 95vh;
  margin: 20px;
  letter-spacing: 1.2px;
}

.recipeList {
  border: 1.5px solid #777;
  padding: 25px;
  font-size: 2rem;
  color: #6C533B;
  font-weight: 300;
  margin-bottom: 40px;
}

.left-section {
  border-right: 1.5px solid #777;
  padding: 30px 25px 0px 25px;
  overflow: scroll;
}

.right-section {
  padding: 60px;
  overflow: scroll;
  overflow-x: auto;
  word-wrap: break-word;
}

.meta-data {
  display: grid;
  grid-template-rows: minmax(5%, 95%);
  grid-template-columns: minmax(1fr, 1fr);
}

.ingredients-item:first-child,
.instructions-item:first-child {
  color: #6C533B;
  font-size: 1.2rem;
  font-weight: bold;
  margin-bottom: 30px;
}

.ingredients-list,
.instructions-list {
  color: #777;
  font-size: 1rem;
  letter-spacing: 0;
  font-weight: 300;
}

.instructions-list{
  font-size: 1.3rem;
  font-weight: 300;
}

.ingredients-list li,
.instructions-list li {
  margin-bottom: 15px;
}

.card-title {
  font-size: 3rem;
  margin-bottom: 90px;
  font-weight: bold;
  grid-column: span 2;
  color: #6C533B;
}

@media only screen and (max-width: 1357px) {
  button {
    display: none;
  }

  .right-section {
    padding: 30px 0 60px 30px;
  }

  .meta-data {
    grid-template-rows: 150px 1fr;
    grid-template-columns: 1fr;
  }

  .card-title {
    grid-column: span 1;
  }

  .ingredients-item:first-child, 
  .instructions-item:first-child {
    margin-bottom: 15px;
  }

  .ingredients-list {
    margin-bottom: 25px;
  }
  .recipeList {
    font-size: 1.5rem;
  }
}

@media only screen and (max-width: 526px) {
  .recipeList {
    font-size: .9rem;
    padding: 10px;
  }

  .card-title {
    font-size: 1.4rem;
  }

  .meta-data {
    grid-template-rows: 90px 1fr;
    grid-template-columns: 1fr;
  }

  .ingredients-item:first-child, 
  .instructions-item:first-child {
    font-size: 1rem;
  }

  .ingredients-list, 
  .instructions-list {
    font-size: .9rem;
  }
}
